import "./BusinessConditions.scss";
import { Link } from "react-router-dom";

const businessConditions = () => {
  return (
    <>
      <section className="business-conditions-page">
        <div className="business-conditions-title">
          <h1>Obchodn podmnky</h1>
        </div>
        <div className="business-conditions-content">
          <h4>Objednvka termnu:</h4>
          <ul>
            <li>Objednat si svj termn na focen lze zde vyplnnm formule v kategorii <Link className="" to="/kontakt">kontakt</Link>. Mete m ale tak kontaktovat do zprvy na socilnch stch, jako je <a href="https://www.facebook.com/Fotografieodhanky" target="_blank" rel="noopener noreferrer">Facebook</a>  nebo <a href="https://www.instagram.com/fotografie_od_hanky/" target="_blank" rel="noopener noreferrer">Instagram</a>.</li>
            <li>Objednvkou termnu vyjadujete souhlas s tmito obchodnmi podmnkami a akceptujete mj autorsk pstup a fotografick styl.</li>
            <li>V ppad nepznivho poas i nemoci z va nebo moj strany, je termn pesunut na nejbli voln termn.</li>
          </ul>
          <h4>Ceny focen a splatnost:</h4>
          <ul>
            <li>Ceny balk najdete v kategorii <Link className="" to="/cenik">Cenk</Link>.</li>
            <li>Po objednn termnu je nutn uhradit rezervan zlohu. O jej pesn vi vs budu pedem informovat.</li>
            <li>Jestlie se klient na focen nedostav, a to bez jakkoli omluvy, rezervan zloha propad.</li>
            <li>Splatnost balku nastv pot, co si na zklad nhled vyberete fotografie, o kter mte zjem.</li>
            <li>Vt zsahy do fotografi dle vaeho pn budou individuln nacenny.</li>
          </ul>
          <h4>Vbr fotografi:</h4>
          <ul>
            <li>Po focen zaslm neupraven nhledy fotografi online. Poet nhled se li a je individuln dle dlky focen a dalch podmnek bhem focen.</li>
            <li>Na vbr budete mt dostatek asu.</li>
            <li>Hned po pijet vbru od vs se poutm do prav.</li>
            <li>U svatebnho focen fotografie k prav vybrm sama.</li>
          </ul>
          <h4>Termn zhotoven a dodn fotografi:</h4>
          <ul>
            <li>Atelirov i exterirov fotografie zpracuji a elektronicky zalu do 5 pracovnch dn od pijet vaeho vbru.</li>
            <li>Svatebn zakzka trv piblin 2-3 tdny od vaeho svatebnho dne.</li>
            <li>Firemn akce se individuln li dle potu fotografi.</li>
            <li>Fotografie pro kolky/koly se opt individuln li dle potu td apod.</li>
          </ul>
          <h4>Formt a pedn fotografi:</h4>
          <ul>
            <li>Upraven a pirozen retuovan fotografie zaslm elektronicky ve formtu JPEG prostednictvm schovny.</li>
            <li>Svatebn fotografie zaslm tak elektronicky ve formtu JPEG prostednictvm schovny. Vechny fotografie ale ode m obdrte i na flash disku.</li>
            <li>Neupraven fotografie neposkytuji!</li>
          </ul>
          <h4>Archivace fotografi:</h4>
          <ul>
            <li>Vae fotografie u m budou uchovny minimln po dobu 6 msc. I tak doporuuji je po staen ihned zlohovat a uloit na bezpen msto.</li>
          </ul>
          <h4>Drkov poukazy:</h4>
          <ul>
            <li>Drkov poukazy lze zakoupit po cel rok k rznm pleitostem, jako jsou narozeniny, Vnoce, vro...</li>
            <li>Kad poukaz m platnost 1 rok od zakoupen.</li>
          </ul>
          <h4>Jin ujednn:</h4>
          <ul>
            <li>Zkaznk nabv k zakoupenm fotografim vlastnick prva, nikoli prva autorsk, kter nle pouze fotografovi. Fotografie tud nesm bt pouity pro jakkoli komern ely, propagaci i prodej tet stran bez psemnho souhlasu autora. Fotografovan osoby mohou pouvat fotografie pro svou nekomern osobn prezentaci.</li>
            <li>Dle autorskho zkona, nen bez souhlasu autora dovoleno, hotov fotografie jakkoliv upravovat (nap. pouit barevnch filtr na instagramu) a zveejovat jako dlo autora.</li>
            <li>Vyhrazuji si prvo ke zveejnn fotografi ve svm portfoliu na socilnch stch, na svch webovch strnkch, pp. dalch propaganch materilech. Pokud si nepejete, aby vae fotografie byly zveejnny, je nutn m o tom pedem informovat. Pokud tak neuinte, m se za to, e s ppadnm zveejnnm souhlaste. Mte samozejm prvo dodaten poadovat odstrann fotografi z portfolia.</li>
          </ul>
        </div>
      </section>
    </>
  )
};

export default businessConditions;