import "./Home.scss";
import imageOne from "../../images/uvodni_foto_mensi.jpeg";
import imageTwo from "../../images/uvodni_foto_vetsi.jpeg";
import imageThree from "../../images/uvodni_foto_hanka.jpg";
import recenze from "../../images/Screenshot_20230510_172917_com.instagram.android_edit_93504492621669.jpg";
import socialSiteImg from "../../images/pozadi_sicialnich_siti.jpg";
import axios from "axios";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";


const settings = {
  dots: true,
  infinite: false,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 4,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
        infinite: true,
        dots: true
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        initialSlide: 2
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};
const Home = () => {
  const categoryUrlProduction = 'https://beige-crab-coat.cyclic.app/api/v1/category';
  const [portfolioCategory, setPortfolioCategory] = useState([]);

  const fetchCategoryData = async () => {
    try {
      const categoryResponse = await axios(categoryUrlProduction);
      // const categoryResponse = await axios(categoryUrl);
      setPortfolioCategory(categoryResponse.data.categories);
    } catch (error) {
      console.log(error.response);
    };
  };
  const updateValues = (categoryName) => {
    const addedSpaces = categoryName.replace(/-+/g, ' ');

    const capitalized =
      addedSpaces.charAt(0).toUpperCase() + addedSpaces.slice(1);
    return capitalized;
  }

  useEffect(() => {
    fetchCategoryData();
  }, []);



  return <div className="home-page">
    <section className="hero-section">
      <div className="tile-images-container">
        <div className="img1"><img src={imageOne} alt="" /></div>
        <div className="img2"><img src={imageTwo} alt="" /></div>
      </div>
      <div className="contact-link-container">
        <Link className="contact-link-homepage" to="/kontakt">Kontaktuj m!</Link>
      </div>
    </section>
    <section className="opening-text-section">
      <div className="opening-text-image">
        <img src={imageThree} alt="" />
      </div>
      <div className="opening-text">
        <div className="opening-text-container">
          <p><strong>BcA. Hana Jedlikov</strong></p>

          <p>Vtm t na svch strnkch. Lb se ti moje prce a chce tak takov fotografie? Mrkni na to, kdo t bude fotit.</p>

          <p><em>Kdo jsem?</em><br />

            Jmenuji se Hana Jedlikov a v oboru fotografie se pohybuji u od dtstv. Ve zaalo, u kdy mi bylo 11 let a j dostala svj prvn fotoapart. Od t doby se fotografie stala moji ivotn cestou.

            Nen to pro m pouze konek i prce. Je to dalo by se ci mj ivotn smr, ve kterm jsem se rozhodla zdokonalovat a vloit do toho kus sebe. Sv znalosti v oboru jsem rozvjela jak na stedn, tak i na vysok kole.</p>
          <p className="home-page-about-me-link"><Link to="/o-mne">ti dle...</Link></p>
        </div>
      </div>
    </section>
    <section className="recenze">
      <h2 className="home-page-title">Vae reakce</h2>
      <div><img src={recenze} alt="" /></div>
    </section>
    <section className="homepage-portfolio">
      <h2 className="home-page-title">Portflio</h2>
      <Slider {...settings}>
        {
          portfolioCategory.map((onePortfolioCategory) => {
            const { _id, categoryName, categoryImage } = onePortfolioCategory;
            const categoryNorme = updateValues(categoryName);
            return (
            <div key={_id} className="card">
              <div className="card-top">
                <img src={categoryImage} alt={categoryNorme} />
                <Link to={`/portfolio/${categoryName}`}>{categoryNorme}</Link>
              </div>
            </div>
            )
          })
        }
      </Slider>
    </section >
    <section className="social-sites-section">
      <div className="social-sites-title">
        <h2 className="home-page-title">Sleduj m na socilnch stch</h2>
      </div>
      <div style={{
        backgroundImage: `url(${socialSiteImg})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }} className="social-site-img">
        <div className="social-site-links">
          <a href="https://www.facebook.com/Fotografieodhanky" target="_blank" rel="noopener noreferrer">FACEBOOK</a>
          <a href="https://www.instagram.com/fotografie_od_hanky/" target="_blank" rel="noopener noreferrer">INSTAGRAM</a>
        </div>
      </div>
    </section>
  </div >
}

export default Home;